.container {
    font-size: 14px;
    position: fixed;
    z-index: 10;
}

.bottom-right {
    bottom: 1rem;
    right: 1rem;
    animation: toast-in-right .7s;
}

.top-right {
    top: 1rem;
    right: 1rem;
    animation: toast-in-right .7s;
}

.top-left {
    top: 1rem;
    left: 1rem;
    animation: toast-in-left .7s;
}

.bottom-left {
    bottom: 1rem;
    left: 1rem;
    animation: toast-in-left .7s;
}

.notification {
    margin-bottom: 1rem;
    border-radius: 4px;
    box-shadow: 0 0 10px #999;
    color: #000;
    opacity: 0.9;
    transition: .3s ease;
}

.notification:hover {
    box-shadow: 0 0 12px #fff;
    opacity: 1;
}

.toast {
    height: 80px;
    width: 385px;
    color: #fff;
}

.container button {
    float: right;
    background: none;
    border: none;
    color: #fff;
    opacity: 0.8;
    cursor: pointer;
}

.title {
    font-weight: 700;
    font-size: 16px;
    text-align: left;
    margin-top: 0;
    margin-bottom: 6px;
    width: 300px;
    height: 18px;
}

.description {
    margin: 0;
    text-align: left;
}

@keyframes toast-in-right {
    from {
        transform: translateX(100%);
    }

    to {
        transform: translateX(0);
    }
}

@keyframes toast-in-left {
    from {
        transform: translateX(0);
    }

    to {
        transform: translateX(100%);
    }
}

.notificationInfo {
    padding: 15px 0px 15px 10px;

}

.cancelButton {
    padding: 10px;
}

.toastContent {
    display: flex;
    align-items: center;
}

.logo {
    padding: 0 5px 0 15px;
}